*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100%;
}

.register-page{
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 0 0 3px gray;
    margin-bottom: 10px;
}

/* transection page */

/* antd icon */
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: blue;
}

.inactive-icon {
  color: gray;
}

.filter-tab .ant-select-selector {
  width: 150px !important;
}